/**
 * Tabelas responsivas
 * 
 * Estilos para tabelas responsivas que usam o módulo ResponsiveTables.
 * As tabelas são envolvidas em um container com scroll horizontal em telas menores.
 */

// Container para tabelas responsivas
.table-responsive {
  display: block;
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  -ms-overflow-style: -ms-autohiding-scrollbar;
  margin-bottom: 1.5rem;
  border: 1px solid #dee2e6;
  border-radius: 4px;
  
  // Adiciona sombra sutil ao rolar
  .table-wrapper {
    position: relative;
  }
  
  // Tabela dentro do container
  table {
    width: 100%;
    margin-bottom: 0;
    background-color: #fff;
    
    // Cabeçalho da tabela
    thead {
      th {
        background-color: #f8f9fa;
        border-bottom: 2px solid #dee2e6;
        vertical-align: bottom;
        padding: 0.75rem;
        text-align: left;
        font-weight: 600;
      }
    }
    
    // Células da tabela
    tbody {
      td {
        padding: 0.75rem;
        vertical-align: top;
        border-top: 1px solid #dee2e6;
      }
      
      tr:hover {
        background-color: rgba(0, 0, 0, 0.02);
      }
    }
    
    // Linhas listradas
    &.table-striped {
      tbody tr:nth-of-type(odd) {
        background-color: rgba(0, 0, 0, 0.02);
      }
    }
    
    // Tabela com bordas
    &.table-bordered {
      border: 1px solid #dee2e6;
      
      th,
      td {
        border: 1px solid #dee2e6;
      }
    }
    
    // Tabela com hover
    &.table-hover {
      tbody tr:hover {
        background-color: rgba(0, 0, 0, 0.05);
      }
    }
  }
  
  // Indicador de scroll (visível apenas em telas pequenas)
  .table-scroll-indicator {
    display: none;
    text-align: center;
    padding: 0.5rem;
    font-size: 0.8rem;
    color: #6c757d;
    background-color: #f8f9fa;
    border-top: 1px solid #dee2e6;
    
    // Mostrar apenas em telas pequenas quando houver scroll
    @media (max-width: 767.98px) {
      display: block;
    }
  }
  
  // Efeito de sombra no final da tabela para indicar conteúdo adicional
  &::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    width: 20px;
    pointer-events: none;
    background: linear-gradient(to right, rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.9));
    opacity: 0;
    transition: opacity 0.3s ease;
  }
  
  // Mostrar sombra quando houver scroll
  &.has-scroll::after {
    opacity: 1;
  }
}

// Classe para desabilitar o comportamento responsivo
.no-responsive {
  white-space: nowrap;
  
  // Garantir que a tabela não quebre em telas pequenas
  @media (max-width: 767.98px) {
    display: inline-block;
    min-width: 100%;
  }
}

// Estilos para tabelas responsivas em dispositivos móveis
@media (max-width: 767.98px) {
  // Forçar quebra de linha em células de tabela em telas pequenas
  .table-responsive {
    table {
      thead {
        display: none; // Esconder cabeçalho em telas pequenas
      }
      
      tbody {
        tr {
          display: block;
          margin-bottom: 1rem;
          border: 1px solid #dee2e6;
          
          td {
            display: flex;
            justify-content: space-between;
            padding: 0.5rem 0.75rem;
            text-align: right;
            border: none;
            border-bottom: 1px solid #dee2e6;
            
            // Adicionar rótulo antes de cada célula
            &::before {
              content: attr(data-label);
              font-weight: 600;
              text-align: left;
              padding-right: 1rem;
              flex: 1;
            }
            
            // Remover borda da última célula
            &:last-child {
              border-bottom: none;
            }
          }
        }
      }
    }
  }
  
  // Estilo para tabelas com a classe .table-mobile-stack
  .table-mobile-stack {
    thead {
      display: none;
    }
    
    tbody {
      tr {
        display: block;
        margin-bottom: 1.5rem;
        border: 1px solid #dee2e6;
        border-radius: 4px;
        overflow: hidden;
        
        td {
          display: flex;
          justify-content: space-between;
          padding: 0.5rem 0.75rem;
          text-align: right;
          border: none;
          border-bottom: 1px solid #dee2e6;
          
          &::before {
            content: attr(data-label);
            font-weight: 600;
            text-align: left;
            padding-right: 1rem;
            flex: 1;
          }
          
          &:last-child {
            border-bottom: none;
          }
        }
      }
    }
  }
}
