/**
 * Variáveis do Tema UENF
 * 
 * Este arquivo contém todas as variáveis globais utilizadas no tema,
 * incluindo cores, tipografia, espaçamentos, breakpoints e outras configurações.
 * 
 * Uso recomendado:
 * - Sempre utilize as variáveis ao invés de valores hardcoded
 * - Consulte este arquivo antes de adicionar novas cores ou valores
 * - Mantenha a organização por seções
 */

// ==========================================================================
// 1. Cores
// ==========================================================================

/**
 * 1.1 Cores Principais
 * Cores principais da identidade visual da UENF
 */
$primary-color: #1D3771;        // Azul UENF principal
$primary-light: #1D3770BF;      // Azul UENF com transparência (70%)
$primary-dark: darken($primary-color, 10%);
$secondary-color: #6c757d;      // Cor secundária (cinza)
$light-color: #f8f9fa;          // Cor de fundo clara
$text-color: #26557D;           // Cor principal para textos
$text-muted: lighten($text-color, 20%); // Texto secundário
$link-color: #26557D;           // Cor dos links
$link-hover-color: #1D3771;     // Cor dos links ao passar o mouse
$white: #FFFFFF;               // Branco puro
$black: #000000;               // Preto puro

/**
 * 1.2 Cores de Feedback
 * Cores para mensagens de status e feedback ao usuário
 */
$success: #28a745;             // Sucesso, confirmação
$success-light: lighten($success, 40%);
$info: #17a2b8;                // Informação
$info-light: lighten($info, 40%);
$warning: #ffc107;             // Alerta, atenção
$warning-light: lighten($warning, 30%);
$danger: #dc3545;              // Erro, perigo
$danger-light: lighten($danger, 40%);

/**
 * 1.3 Cores de Fundo
 * Cores para os principais elementos de layout
 */
$body-bg: #ffffff;             // Cor de fundo do body
$header-bg: #f8f9fa;           // Cor de fundo do cabeçalho
$footer-bg: #f8f9fa;           // Cor de fundo do rodapé
$border-color: #dee2e6;        // Cor das bordas
$light-bg: #f8f9fa;            // Fundo claro para seções alternadas

// ==========================================================================
// 2. Tipografia
// ==========================================================================

/**
 * 2.1 Famílias de Fontes
 * Definição das fontes utilizadas no tema
 */
$font-primary: 'Ubuntu', system-ui, -apple-system, Roboto, Oxygen, sans-serif;
$font-secondary: system-ui, -apple-system, Roboto, Oxygen, sans-serif;
$font-monospace: 'SFMono-Regular', Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;

/**
 * 2.2 Tamanhos de Fonte
 * Sistema de escala modular baseado em 16px (1rem)
 */
$font-size-base: 1rem;                  // 16px - Tamanho base
$font-size-xs: $font-size-base * 0.75;  // 12px - Textos muito pequenos
$font-size-sm: $font-size-base * 0.875; // 14px - Textos pequenos
$font-size-md: $font-size-base;         // 16px - Texto base
$font-size-lg: $font-size-base * 1.25;  // 20px - Subtítulos
$font-size-xl: $font-size-base * 1.5;   // 24px - Títulos médios
$font-size-xxl: $font-size-base * 2;    // 32px - Títulos grandes
$font-size-xxxl: $font-size-base * 2.5; // 40px - Títulos muito grandes

/**
 * 2.3 Pesos de Fonte
 * Padronização dos pesos de fonte utilizados
 */
$font-weight-light: 300;    // Fonte leve
$font-weight-normal: 400;   // Fonte normal (padrão)
$font-weight-medium: 500;   // Fonte média (semi-negrito)
$font-weight-semibold: 600; // Fonte semi-negrito
$font-weight-bold: 700;     // Fonte negrito

/**
 * 2.4 Altura de Linha
 * Controle do espaçamento vertical entre linhas
 */
$line-height-base: 1.5;     // Altura de linha base (24/16)
$line-height-sm: 1.25;      // Altura de linha reduzida
$line-height-lg: 1.75;      // Altura de linha aumentada

// Bordas e raios
$btn-border-radius: 0.25rem;
$btn-border-radius-lg: 0.3rem;
$btn-border-radius-sm: 0.2rem;

// Raio da borda dos botões
$btn-border-radius: 0.25rem;
$btn-border-radius-lg: 0.3rem;
$btn-border-radius-sm: 0.2rem;

// ==========================================================================
// Espaçamentos
// ==========================================================================

$spacer: 1rem;
$spacers: (
  0: 0,
  1: $spacer * 0.25,
  2: $spacer * 0.5,
  3: $spacer,
  4: $spacer * 1.5,
  5: $spacer * 3,
);

// ==========================================================================
// Breakpoints
// ==========================================================================

$breakpoints: (
  'xs': 0,
  'sm': 576px,
  'md': 768px,
  'lg': 992px,
  'xl': 1200px,
  'xxl': 1400px
);

// ==========================================================================
// Bordas
// ==========================================================================

$border-radius: 0.25rem;
$border-radius-sm: 0.2rem;
$border-radius-lg: 0.3rem;
$border-radius-pill: 50rem;

// ==========================================================================
// Transições
// ==========================================================================

$transition-base: all 0.2s ease-in-out;
$transition-fade: opacity 0.15s linear;
$transition-collapse: height 0.35s ease;

// ==========================================================================
 // Z-index
// ==========================================================================

$zindex-dropdown: 1000;
$zindex-sticky: 1020;
$zindex-fixed: 1030;
$zindex-modal-backdrop: 1040;
$zindex-modal: 1050;
$zindex-popover: 1060;
$zindex-tooltip: 1070;
