/**
 * Componente: Comentários
 * Estilos para a seção de comentários do WordPress
 */

.comments-area {
  margin-top: 3rem;
  padding-top: 2rem;
  border-top: 1px solid #eee;

  // Título da seção de comentários
  .comments-title {
    font-size: 1.5rem;
    margin: 0 0 2rem;
    color: $primary-color;
    font-weight: $font-weight-bold;
    
    span {
      color: $text-color;
      font-size: 1rem;
      font-weight: $font-weight-normal;
    }
  }

  // Lista de comentários
  .comment-list {
    list-style: none;
    margin: 0 0 3rem;
    padding: 0;

    // Comentário individual
    .comment,
    .pingback,
    .trackback {
      margin-bottom: 2rem;
      position: relative;
      padding: 1.5rem;
      background-color: #f8f9fa;
      border-radius: $btn-border-radius;
      
      // Comentário em resposta
      .children {
        list-style: none;
        margin: 1.5rem 0 0 2rem;
        padding: 0;
        
        @media (max-width: 575px) {
          margin-left: 1rem;
        }
      }
    }

    // Cabeçalho do comentário
    .comment-header {
      display: flex;
      align-items: center;
      margin-bottom: 1rem;

      // Avatar do autor
      .avatar {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        margin-right: 1rem;
      }

      // Metadados do comentário
      .comment-meta {
        flex: 1;

        .comment-author {
          margin: 0 0 0.25rem;
          font-size: 1rem;
          font-weight: $font-weight-bold;
          line-height: 1.3;

          a {
            color: $text-color;
            text-decoration: none;
            
            &:hover {
              color: $primary-color;
              text-decoration: underline;
            }
          }

          .says {
            display: none;
          }
        }

        .comment-metadata {
          font-size: 0.8125rem;
          color: #6c757d;
          
          a {
            color: inherit;
            text-decoration: none;
            
            &:hover {
              color: $primary-color;
              text-decoration: underline;
            }
          }

          .edit-link {
            margin-left: 0.5rem;
            
            &::before {
              content: '·';
              margin-right: 0.5rem;
            }
          }
        }
      }
    }

    // Conteúdo do comentário
    .comment-content {
      margin-left: 66px; // Avatar width + margin
      
      @media (max-width: 575px) {
        margin-left: 0;
        margin-top: 1rem;
      }

      p {
        margin: 0 0 1rem;
        line-height: 1.7;
        
        &:last-child {
          margin-bottom: 0;
        }
      }
    }

    // Link de resposta
    .reply {
      margin-top: 1rem;
      text-align: right;
      
      .comment-reply-link {
        display: inline-flex;
        align-items: center;
        font-size: 0.875rem;
        color: $primary-color;
        text-decoration: none;
        font-weight: $font-weight-medium;
        
        &:hover {
          text-decoration: underline;
        }
        
        &::before {
          content: '\f3e5';
          font-family: 'Font Awesome 5 Free';
          font-weight: 900;
          margin-right: 0.5rem;
          font-size: 0.8125rem;
        }
      }
    }
  }

  // Navegação de comentários
  .comment-navigation {
    margin: 2rem 0;
    display: flex;
    justify-content: space-between;
    
    .nav-previous,
    .nav-next {
      a {
        display: inline-flex;
        align-items: center;
        color: $text-color;
        text-decoration: none;
        font-weight: $font-weight-medium;
        
        &:hover {
          color: $primary-color;
        }
      }
    }
    
    .nav-previous a::before {
      content: '\f053';
      font-family: 'Font Awesome 5 Free';
      font-weight: 900;
      margin-right: 0.5rem;
      font-size: 0.8125rem;
    }
    
    .nav-next a::after {
      content: '\f054';
      font-family: 'Font Awesome 5 Free';
      font-weight: 900;
      margin-left: 0.5rem;
      font-size: 0.8125rem;
    }
  }

  // Formulário de resposta
  .comment-respond {
    margin-top: 3rem;
    
    .comment-reply-title {
      font-size: 1.5rem;
      margin: 0 0 1.5rem;
      color: $primary-color;
      font-weight: $font-weight-bold;
      
      small {
        margin-left: 1rem;
        
        a {
          color: $text-color;
          font-size: 0.875rem;
          font-weight: $font-weight-normal;
          text-decoration: none;
          
          &:hover {
            color: $primary-color;
            text-decoration: underline;
          }
        }
      }
    }
    
    .comment-form {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 1.5rem;
      
      @media (max-width: 767px) {
        grid-template-columns: 1fr;
      }
      
      .comment-notes,
      .logged-in-as,
      .form-submit,
      .comment-form-comment,
      .comment-form-author,
      .comment-form-email,
      .comment-form-url,
      .comment-form-cookies-consent {
        grid-column: 1 / -1;
      }
      
      .comment-form-author,
      .comment-form-email {
        @media (min-width: 768px) {
          grid-column: span 1;
        }
      }
      
      .form-submit {
        margin: 0;
      }
      
      .form-submit input[type="submit"] {
        background-color: $primary-color;
        color: $white;
        border: none;
        padding: 0.75rem 2rem;
        border-radius: $btn-border-radius;
        font-weight: $font-weight-medium;
        cursor: pointer;
        transition: background-color 0.2s ease;
        
        &:hover {
          background-color: darken($primary-color, 10%);
        }
      }
      
      .comment-form-cookies-consent {
        display: flex;
        align-items: center;
        
        input[type="checkbox"] {
          margin-right: 0.5rem;
        }
        
        label {
          margin: 0;
          font-size: 0.875rem;
          color: #6c757d;
        }
      }
      
      label {
        display: block;
        margin-bottom: 0.5rem;
        font-weight: $font-weight-medium;
        
        .required {
          color: #dc3545;
          margin-left: 0.25rem;
        }
      }
      
      input[type="text"],
      input[type="email"],
      input[type="url"],
      textarea {
        width: 100%;
        padding: 0.75rem 1rem;
        border: 1px solid #dee2e6;
        border-radius: $btn-border-radius;
        font-size: 0.9375rem;
        transition: border-color 0.2s ease, box-shadow 0.2s ease;
        
        &:focus {
          border-color: $primary-color;
          outline: none;
          box-shadow: 0 0 0 0.2rem rgba($primary-color, 0.25);
        }
      }
      
      textarea {
        min-height: 150px;
        resize: vertical;
      }
    }
  }
}

// Mensagem quando os comentários estão fechados
.comments-closed {
  font-style: italic;
  color: #6c757d;
  margin: 2rem 0;
  text-align: center;
  font-size: 0.9375rem;
}

// Estilo para comentários em páginas específicas
.single .comments-area,
.page .comments-area {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}
