/**
 * Layout: Rodapé
 * Estilos para o rodapé do site
 */

.site-footer {
  background-color: $primary-color;
  color: $white;
  padding: 3rem 0 1.5rem;
  margin-top: 3rem;
  font-size: 0.9375rem;
  line-height: 1.6;

  a {
    color: rgba($white, 0.8);
    text-decoration: none;
    transition: color 0.2s ease;

    &:hover {
      color: $white;
      text-decoration: underline;
    }
  }

  .footer-widgets {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 2rem;
    margin-bottom: 2.5rem;

    .widget {
      margin-bottom: 1.5rem;

      .widget-title {
        color: $white;
        font-size: 1.125rem;
        margin-top: 0;
        margin-bottom: 1.25rem;
        padding-bottom: 0.75rem;
        border-bottom: 1px solid rgba($white, 0.1);
        font-weight: $font-weight-bold;
      }

      ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
          margin-bottom: 0.5rem;
          padding-bottom: 0.5rem;
          border-bottom: 1px solid rgba($white, 0.05);

          &:last-child {
            margin-bottom: 0;
            padding-bottom: 0;
            border-bottom: none;
          }
        }
      }
    }
  }

  .footer-bottom {
    border-top: 1px solid rgba($white, 0.1);
    padding-top: 1.5rem;
    text-align: center;
    font-size: 0.875rem;
    color: rgba($white, 0.7);

    .footer-links {
      margin-top: 1rem;

      a {
        margin: 0 0.5rem;
        display: inline-block;

        &:first-child {
          margin-left: 0;
        }
      }
    }
  }

  @media (max-width: 767px) {
    .footer-widgets {
      grid-template-columns: 1fr;
      gap: 2rem 0;
    }

    .footer-bottom {
      text-align: left;

      .footer-links {
        margin-top: 1rem;

        a {
          display: block;
          margin: 0.5rem 0;
          padding: 0.25rem 0;

          &:first-child {
            margin-top: 0;
            padding-top: 0;
          }
        }
      }
    }
  }
}
