/**
 * Página: Post Único
 * Estilos para páginas de post único
 */

.single-post {
  .entry-header {
    margin-bottom: 2rem;
    text-align: center;
    
    .entry-title {
      font-size: 2rem;
      color: $primary-color;
      margin-bottom: 1rem;
    }
    
    .entry-meta {
      color: #6c757d;
      font-size: 0.9375rem;
      margin-bottom: 1.5rem;
      
      span {
        margin: 0 0.75rem 0.5rem 0;
        
        a {
          color: inherit;
          &:hover { color: $primary-color; }
        }
      }
    }
  }
  
  .entry-content {
    line-height: 1.8;
    
    p { margin-bottom: 1.5rem; }
    
    h2, h3, h4 {
      margin: 2rem 0 1.25rem;
      color: $primary-color;
    }
    
    h2 { font-size: 1.75rem; }
    h3 { font-size: 1.5rem; }
    h4 { font-size: 1.25rem; }
    
    a {
      color: $link-color;
      &:hover { color: $link-hover-color; }
    }
    
    blockquote {
      border-left: 4px solid $primary-color;
      margin: 2rem 0;
      padding: 1.5rem;
      background: #f8f9fa;
      font-style: italic;
    }
    
    img {
      max-width: 100%;
      height: auto;
      border-radius: $btn-border-radius;
      margin: 1.5rem 0;
    }
    
    ul, ol {
      margin: 1.5rem 0;
      padding-left: 1.5rem;
      
      li { margin-bottom: 0.5rem; }
    }
  }
  
  .entry-footer {
    margin-top: 3rem;
    padding-top: 1.5rem;
    border-top: 1px solid #eee;
    
    .post-tags {
      margin-bottom: 1.5rem;
      
      .tags-links {
        display: flex;
        flex-wrap: wrap;
        gap: 0.5rem;
        
        a {
          background: #f8f9fa;
          color: $text-color;
          padding: 0.25rem 0.75rem;
          border-radius: 50px;
          font-size: 0.8125rem;
          text-decoration: none;
          
          &:hover {
            background: $primary-color;
            color: white;
          }
        }
      }
    }
  }
  
  .post-navigation {
    margin: 3rem 0;
    padding: 1.5rem 0;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    
    .nav-links {
      display: flex;
      justify-content: space-between;
      
      .nav-previous,
      .nav-next {
        a {
          display: flex;
          align-items: center;
          color: $text-color;
          text-decoration: none;
          
          &:hover { color: $primary-color; }
          
          span { display: block; }
          
          .nav-subtitle {
            font-size: 0.8125rem;
            color: #6c757d;
          }
        }
      }
      
      .nav-previous a { text-align: left; }
      .nav-next a { text-align: right; }
    }
  }
}
