/**
 * Página: Home
 * Estilos específicos para a página inicial
 */

// Seção hero
.hero-section {
  padding: 3rem 0;
  background: linear-gradient(135deg, rgba($primary-color, 0.1) 0%, rgba($primary-color, 0.3) 100%);
  text-align: center;
  margin-bottom: 2rem;
  
  h1 {
    font-size: 2.5rem;
    color: $primary-color;
    margin-bottom: 1rem;
    
    @media (min-width: 768px) {
      font-size: 3rem;
    }
  }
  
  p.lead {
    font-size: 1.25rem;
    max-width: 800px;
    margin: 0 auto 2rem;
  }
  
  .btn {
    margin: 0 0.5rem;
  }
}

// Seção de destaques
.featured-section {
  padding: 2rem 0;
  
  .section-title {
    text-align: center;
    margin-bottom: 2rem;
    
    h2 {
      position: relative;
      display: inline-block;
      
      &::after {
        content: '';
        position: absolute;
        bottom: -10px;
        left: 50%;
        transform: translateX(-50%);
        width: 60px;
        height: 3px;
        background: $primary-color;
      }
    }
  }
  
  .featured-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 1.5rem;
    
    .featured-item {
      background: $white;
      border-radius: $btn-border-radius;
      overflow: hidden;
      box-shadow: 0 2px 15px rgba(0,0,0,0.05);
      transition: transform 0.3s ease;
      
      &:hover {
        transform: translateY(-5px);
      }
      
      .featured-image {
        height: 180px;
        overflow: hidden;
        
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          transition: transform 0.5s ease;
        }
      }
      
      .featured-content {
        padding: 1.25rem;
        
        h3 {
          margin: 0 0 0.75rem;
          font-size: 1.1rem;
          
          a {
            color: $text-color;
            text-decoration: none;
            
            &:hover {
              color: $primary-color;
            }
          }
        }
      }
    }
  }
}

// Seção de notícias
.news-section {
  padding: 2rem 0;
  background: #f8f9fa;
  
  .section-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;
    
    h2 {
      margin: 0;
    }
    
    .view-all {
      color: $primary-color;
      text-decoration: none;
      font-weight: $font-weight-medium;
    }
  }
  
  .news-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 1.5rem;
  }
}

// Seção CTA
.cta-section {
  padding: 3rem 0;
  background: $primary-color;
  color: $white;
  text-align: center;
  
  h2 {
    color: $white;
    margin-bottom: 1.5rem;
  }
  
  p {
    margin-bottom: 1.5rem;
    font-size: 1.1rem;
    opacity: 0.9;
  }
  
  .btn {
    background: $white;
    color: $primary-color;
    
    &:hover {
      background: darken($white, 10%);
    }
  }
}
