/**
 * Tabelas
 * Estilos básicos para tabelas
 */

// Estilos base para tabelas
// Estes estilos são complementados pelos estilos em components/_tables.scss

table {
  width: 100%;
  max-width: 100%;
  margin-bottom: 1rem;
  background-color: transparent;
  border-collapse: collapse;
  
  th,
  td {
    padding: 0.75rem;
    vertical-align: top;
    border-top: 1px solid #dee2e6;
  }
  
  thead th {
    vertical-align: bottom;
    border-bottom: 2px solid #dee2e6;
    background-color: #f8f9fa;
    font-weight: 600;
  }
  
  tbody + tbody {
    border-top: 2px solid #dee2e6;
  }
}

// Tabela com linhas listradas
table.striped {
  tbody tr:nth-of-type(odd) {
    background-color: rgba(0, 0, 0, 0.02);
  }
}

// Tabela com bordas
table.bordered {
  border: 1px solid #dee2e6;
  
  th,
  td {
    border: 1px solid #dee2e6;
  }
}

// Tabela com hover
table.hoverable {
  tbody tr:hover {
    background-color: rgba(0, 0, 0, 0.05);
  }
}

// Tabela compacta
table.compact {
  th,
  td {
    padding: 0.3rem;
  }
}

// Classes auxiliares para alinhamento de texto
.table-text-left {
  text-align: left !important;
}

.table-text-center {
  text-align: center !important;
}

.table-text-right {
  text-align: right !important;
}

// Responsividade básica para tabelas
@media (max-width: 767.98px) {
  // Forçar quebra de linha em células de tabela em telas pequenas
  table.responsive {
    thead {
      display: none; // Esconder cabeçalho em telas pequenas
    }
    
    tbody {
      tr {
        display: block;
        margin-bottom: 1rem;
        border: 1px solid #dee2e6;
        
        td {
          display: flex;
          justify-content: space-between;
          padding: 0.5rem 0.75rem;
          text-align: right;
          border: none;
          border-bottom: 1px solid #dee2e6;
          
          // Adicionar rótulo antes de cada célula
          &::before {
            content: attr(data-label);
            font-weight: 600;
            text-align: left;
            padding-right: 1rem;
            flex: 1;
          }
          
          // Remover borda da última célula
          &:last-child {
            border-bottom: none;
          }
        }
      }
    }
  }
}
