/**
 * Componente: Menu Offcanvas
 * 
 * Estilos específicos para o menu offcanvas em dispositivos móveis, incluindo:
 * - Barra de rolagem personalizada
 * - Animações de abertura/fechamento
 * - Estilos específicos para o menu em dispositivos móveis
 */

// Container principal do menu offcanvas
.offcanvas {
  // Estilos para o corpo do menu offcanvas
  &-body {
    scrollbar-width: thin;
    scrollbar-color: rgba(29, 55, 113, 0.4) transparent;
    overflow-y: auto;
    padding-right: 5px;
    
    // Estilo da barra de rolagem para WebKit (Chrome, Safari, Edge)
    &::-webkit-scrollbar {
      width: 8px;
    }

    &::-webkit-scrollbar-track {
      background: transparent;
      margin: 0;
    }

    &::-webkit-scrollbar-thumb {
      background: rgba(29, 55, 113, 0.3);
      border-radius: 4px;
      border: 2px solid transparent;
      background-clip: padding-box;
      
      &:hover {
        background: rgba(29, 55, 113, 0.5);
      }
    }
  }

  // Estilo para navegadores Firefox
  @supports (scrollbar-width: thin) {
    &-body {
      scrollbar-width: thin;
      scrollbar-color: rgba(29, 55, 113, 0.3) transparent;
    }
  }
}

// Container do menu principal dentro do offcanvas
.offcanvas-menu {
  // Estilo para submenus no offcanvas
  .sub-menu,
  .children {
    position: relative;
    border-left: 3px solid rgba(255, 255, 255, 0.1);
    margin-left: 15px;
    padding-left: 5px;
    overflow: hidden;
    transition: all 0.3s ease;
    max-height: 0;
    opacity: 0;
    visibility: hidden;
    
    // Estilo quando o submenu está visível
    &.show {
      max-height: 2000px; // Valor alto o suficiente para acomodar o conteúdo
      opacity: 1;
      visibility: visible;
    }
    
    // Itens do submenu
    li {
      a {
        padding-left: 1.5rem;
        font-size: 0.9em;
        color: rgba(255, 255, 255, 0.8);
        transition: all 0.2s ease;
        
        &:hover {
          color: #fff;
          background-color: rgba(255, 255, 255, 0.1);
        }
      }
      
      // Subníveis adicionais
      .sub-menu,
      .children {
        margin-left: 10px;
        
        li a {
          padding-left: 2rem;
        }
      }
    }
  }
  
  // Botão de toggle para submenus
  .menu-item-has-children > a {
    position: relative;
    padding-right: 2.5rem;
    
    &::after {
      content: '\f107';
      font-family: 'Font Awesome 5 Free';
      font-weight: 900;
      position: absolute;
      right: 1rem;
      top: 50%;
      transform: translateY(-50%);
      transition: transform 0.3s ease;
    }
    
    &[aria-expanded="true"]::after {
      transform: translateY(-50%) rotate(180deg);
    }
  }
  
  // Estilo dos itens do menu
  .nav-link {
    display: block;
    color: #fff;
    padding: 0.75rem 1.5rem;
    font-weight: 500;
    transition: all 0.2s ease;
    
    &:hover {
      background-color: rgba(255, 255, 255, 0.1);
      text-decoration: none;
    }
    
    &.active {
      background-color: rgba(255, 255, 255, 0.15);
      font-weight: 600;
    }
  }
}

// Estilos específicos para o cabeçalho do offcanvas
offcanvas-header {
  padding: 1rem 1.5rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  
  .btn-close {
    background: transparent url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='%23fff'%3e%3cpath d='M.293.293a1 1 0 011.414 0L8 6.586 14.293.293a1 1 0 111.414 1.414L9.414 8l6.293 6.293a1 1 0 01-1.414 1.414L8 9.414l-6.293 6.293a1 1 0 01-1.414-1.414L6.586 8 .293 1.707a1 1 0 010-1.414z'/%3e%3c/svg%3e") center/1em auto no-repeat;
    opacity: 1;
    padding: 0.5rem;
    margin: -0.5rem -0.5rem -0.5rem auto;
  }
}

// Ajustes para tema escuro/claro
[data-bs-theme="dark"] {
  .offcanvas {
    background-color: var(--bs-dark);
    
    .offcanvas-menu {
      .nav-link {
        color: var(--bs-light);
        
        &:hover {
          background-color: rgba(255, 255, 255, 0.1);
        }
      }
    }
  }
}
