/**
 * Layout: Conteúdo Principal
 * Estilos para a área de conteúdo principal
 */

.site-content {
  padding: 2rem 0;
  
  .container {
    display: grid;
    grid-template-columns: 1fr 300px;
    gap: 2rem;
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 1rem;
    
    @media (max-width: 991px) {
      grid-template-columns: 1fr;
    }
  }
  
  .content-area {
    article {
      margin-bottom: 2.5rem;
      padding-bottom: 2rem;
      border-bottom: 1px solid #eee;
      
      .entry-title {
        margin: 0 0 1rem;
        font-size: 2rem;
        color: $primary-color;
      }
      
      .entry-meta {
        font-size: 0.875rem;
        color: #6c757d;
        margin-bottom: 1rem;
      }
      
      .entry-content {
        line-height: 1.7;
        
        p { margin-bottom: 1.5rem; }
        
        h2, h3, h4, h5, h6 {
          margin: 2rem 0 1rem;
          color: $primary-color;
        }
        
        a {
          color: $link-color;
          &:hover { color: $link-hover-color; }
        }
      }
    }
  }
  
  .widget-area {
    @media (max-width: 991px) {
      margin-top: 2rem;
      padding-top: 2rem;
      border-top: 1px solid #eee;
    }
  }
}
