/**
 * Layout: Sidebar
 * Estilos para a barra lateral do site
 */

.sidebar {
  padding: 1.5rem;
  background-color: #f8f9fa;
  border-radius: $btn-border-radius;
  margin-bottom: 2rem;

  .widget {
    margin-bottom: 2rem;

    &:last-child {
      margin-bottom: 0;
    }

    .widget-title {
      font-size: 1.25rem;
      margin-top: 0;
      margin-bottom: 1.25rem;
      padding-bottom: 0.75rem;
      border-bottom: 2px solid $primary-color;
      color: $primary-color;
      font-weight: $font-weight-bold;
    }

    ul {
      list-style: none;
      margin: 0;
      padding: 0;

      li {
        margin-bottom: 0.5rem;
        padding-bottom: 0.5rem;
        border-bottom: 1px solid rgba(0, 0, 0, 0.05);

        &:last-child {
          margin-bottom: 0;
          padding-bottom: 0;
          border-bottom: none;
        }

        a {
          color: $text-color;
          text-decoration: none;
          transition: color 0.2s ease;
          display: block;
          padding: 0.25rem 0;

          &:hover {
            color: $primary-color;
            text-decoration: none;
          }
        }
      }
    }

    // Estilos específicos para diferentes tipos de widgets
    &.widget_search {
      .search-form {
        position: relative;

        .search-field {
          width: 100%;
          padding: 0.75rem 1rem;
          border: 1px solid #dee2e6;
          border-radius: $btn-border-radius;
          font-size: 0.9375rem;
          transition: border-color 0.2s ease;

          &:focus {
            border-color: $primary-color;
            outline: none;
            box-shadow: 0 0 0 0.2rem rgba($primary-color, 0.25);
          }
        }

        .search-submit {
          position: absolute;
          right: 0.5rem;
          top: 50%;
          transform: translateY(-50%);
          background: none;
          border: none;
          color: $text-color;
          cursor: pointer;
          padding: 0.5rem;

          &:hover {
            color: $primary-color;
          }
        }
      }
    }

    // Widget de categorias e arquivos
    &.widget_categories,
    &.widget_archive,
    &.widget_pages,
    &.widget_meta {
      ul {
        li {
          position: relative;
          padding-left: 1.25rem;

          &::before {
            content: '\f054';
            font-family: 'Font Awesome 5 Free';
            font-weight: 900;
            font-size: 0.625rem;
            position: absolute;
            left: 0;
            top: 0.5rem;
            color: $primary-color;
          }
        }
      }
    }

    // Widget de tags
    &.widget_tag_cloud {
      .tagcloud {
        display: flex;
        flex-wrap: wrap;
        gap: 0.5rem;

        a {
          display: inline-block;
          background-color: $white;
          color: $text-color;
          padding: 0.25rem 0.75rem;
          border-radius: $btn-border-radius;
          font-size: 0.8125rem !important;
          margin: 0;
          border: 1px solid #dee2e6;
          transition: all 0.2s ease;

          &:hover {
            background-color: $primary-color;
            color: $white;
            border-color: $primary-color;
            text-decoration: none;
          }
        }
      }
    }
  }

  @media (max-width: 991px) {
    margin-top: 2rem;
  }
}
