// Importa variáveis globais e de navegação com namespaces específicos
@use '../config/variables' as vars;
@use './navigation-vars' as nav;

// Usa as variáveis globais com o namespace
$primary-color: vars.$primary-color;

/**
 * Componente: Navegação Principal
 * 
 * Estilo mobile-first com melhorias progressivas para desktop.
 * Utiliza variáveis do arquivo _navigation-vars.scss para consistência.
 */

.main-navigation {
  display: flex;
  align-items: center;
  width: 100%;
  
  // Lista principal do menu - escondida por padrão no mobile
  .menu {
    @include reset-list;
    display: none;
    width: 100%;
    
    // Estilos para desktop
    @include respond-to('lg') {
      display: flex;
      flex-wrap: wrap;
    }
    
    // Itens do menu principal
    .menu-item {
      position: relative;
      margin: 0 0.5rem;
      
      > a {
        display: block;
        padding: nav.$nav-item-padding;
        color: nav.$nav-text-color;
        text-decoration: none;
        font-weight: $font-weight-medium;
        transition: nav.$nav-transition;
        
        &:hover,
        &:focus {
          color: nav.$nav-hover-color;
          background-color: rgba(nav.$nav-hover-color, 0.05);
        }
      }
      
      // Estado ativo do item de menu
      &.current-menu-item,
      &.current-page-ancestor {
        > a {
          color: nav.$nav-hover-color;
          font-weight: $font-weight-bold;
          position: relative;
          
          // Indicador visual do item ativo
          &::after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
            width: nav.$nav-indicator-width;
            height: nav.$nav-indicator-height;
            background: nav.$nav-active-indicator;
            border-radius: nav.$nav-active-border-radius;
            
            // Esconder indicador em mobile
            @include respond-to('max-lg') {
              display: none;
            }
          }
        }
      }
      
      // Submenu - Nível 1
      .sub-menu {
        @include reset-list;
        position: absolute;
        top: 100%;
        left: 0;
        min-width: 240px;
        background: nav.$nav-bg-color;
        box-shadow: nav.$nav-box-shadow;
        border-radius: nav.$nav-border-radius;
        z-index: nav.$nav-z-index;
        margin: 0;
        padding: nav.$nav-submenu-padding;
        opacity: 0;
        visibility: hidden;
        transform: translateY(10px);
        transition: nav.$nav-transition;
        
        .menu-item {
          margin: 0;
          
          a {
            display: block;
            padding: nav.$nav-submenu-item-padding;
            color: nav.$nav-text-color;
            white-space: nowrap;
            transition: nav.$nav-transition;
            
            &:hover,
            &:focus {
              color: nav.$nav-hover-color;
              background-color: rgba(nav.$nav-hover-color, 0.05);
            }
          }
          
          // Subníveis aninhados
          .sub-menu {
            top: 0;
            left: 100%;
            margin-top: -0.5rem;
            border-radius: 0 nav.$nav-border-radius nav.$nav-border-radius 0;
            
            // Ajuste para submenus no lado esquerdo
            &.sub-menu-left {
              left: auto;
              right: 100%;
              margin-right: 1px;
              border-radius: nav.$nav-border-radius 0 0 nav.$nav-border-radius;
            }
          }
        }
      }
      
      // Mostrar submenu ao passar o mouse (apenas desktop)
      @include respond-to('lg') {
        &:hover > .sub-menu {
          opacity: 1;
          visibility: visible;
          transform: translateY(0);
        }
      }
    }
  }
  
  // Botão de toggle do menu móvel
  .menu-toggle {
    display: flex; // Visível por padrão no mobile
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: none;
    border: 2px solid transparent;
    color: nav.$nav-text-color;
    font-size: 1.5rem;
    cursor: pointer;
    padding: 0.75rem;
    margin-left: auto; // Alinha à direita
    border-radius: 4px;
    transition: nav.$nav-transition;
    
    // Ícone do botão (hamburguer)
    .icon-bar {
      display: block;
      width: 22px;
      height: 2px;
      background: currentColor;
      border-radius: 1px;
      transition: nav.$nav-transition;
      
      & + .icon-bar {
        margin-top: 4px;
      }
    }
    
    // Estados de interação
    &:hover,
    &:focus {
      color: nav.$nav-hover-color;
      background-color: rgba(nav.$nav-hover-color, 0.05);
    }
    
    &:focus {
      outline: none;
      border-color: nav.$nav-hover-color;
    }
    
    // Estado ativo (menu aberto)
    &.active {
      .icon-bar {
        &:nth-child(1) {
          transform: translateY(6px) rotate(45deg);
        }
        
        &:nth-child(2) {
          opacity: 0;
        }
        
        &:nth-child(3) {
          transform: translateY(-6px) rotate(-45deg);
        }
      }
    }
    
    // Esconder em telas grandes
    @include respond-to('lg') {
      display: none;
    }
  }
  
  // Estilos específicos para o menu em telas pequenas
  @include respond-to('max-lg') {
    // Menu principal no mobile
    .menu {
      display: none; // Escondido por padrão
      flex-direction: column;
      position: absolute;
      top: 100%;
      left: 0;
      right: 0;
      max-height: calc(100vh - 100%);
      overflow-y: auto;
      background: nav.$nav-bg-color;
      box-shadow: nav.$nav-mobile-shadow;
      z-index: nav.$nav-mobile-z-index;
      padding: 0.5rem 0;
      
      // Menu visível (quando aberto)
      &.toggled {
        display: flex;
      }
      
      // Itens do menu no mobile
      .menu-item {
        margin: 0;
        border-bottom: 1px solid nav.$nav-border-color;
        
        > a {
          padding: 1rem 1.5rem;
          
          // Indicador de submenu
          &::after {
            content: '';
            display: none; // Esconde o indicador de item ativo no mobile
          }
        }
        
        // Submenus no mobile
        .sub-menu {
          position: static;
          display: none;
          box-shadow: none;
          padding: 0;
          opacity: 1;
          visibility: visible;
          transform: none;
          background-color: rgba(0, 0, 0, 0.02);
          border-radius: 0;
          
          // Submenu visível
          &.toggled {
            display: block;
          }
          
          // Itens de submenu
          .menu-item {
            a {
              padding-left: 2.5rem; // Indentação para subníveis
              font-size: 0.95em;
            }
            
            // Subníveis mais profundos
            .menu-item a {
              padding-left: 3.5rem;
            }
          }
        }
        
        // Indicador de submenu (seta)
        &.menu-item-has-children > a {
          position: relative;
          
          &::after {
            content: nav.$nav-arrow-down;
            position: absolute;
            right: 1.5rem;
            top: 50%;
            transform: translateY(-50%);
            font-size: 1.2em;
            line-height: 1;
            transition: transform 0.2s ease;
          }
          
          // Estado aberto
          &.toggled::after {
            content: nav.$nav-arrow-up;
          }
        }
      }
    }
  }
  
  // Estilos para telas grandes (desktop)
  @include respond-to('lg') {
    // Menu principal no desktop
    .menu {
      display: flex;
      flex-wrap: wrap;
      
      // Itens do menu no desktop
      .menu-item {
        // Submenu aberto no hover
        &:hover > .sub-menu {
          opacity: 1;
          visibility: visible;
          transform: translateY(0);
        }
      }
    }
  }
}

// Navegação de breadcrumbs
.breadcrumbs {
  padding: 0.5rem 0;
  font-size: 0.9rem;
  color: lighten($text-color, 30%);
  
  a {
    color: $primary-color;
    text-decoration: none;
    
    &:hover {
      text-decoration: underline;
    }
  }
  
  .separator {
    margin: 0 0.5rem;
    color: lighten($text-color, 40%);
  }
}

// Navegação de paginação
.pagination {
  display: flex;
  justify-content: center;
  margin: 2rem 0;
  
  .page-numbers {
    display: inline-block;
    padding: 0.5rem 1rem;
    margin: 0 0.25rem;
    border: 1px solid #ddd;
    color: $text-color;
    text-decoration: none;
    transition: all 0.2s ease;
    
    &:hover,
    &:focus {
      background: $primary-color;
      color: $white;
      border-color: $primary-color;
    }
    
    &.current {
      background: $primary-color;
      color: $white;
      border-color: $primary-color;
      font-weight: $font-weight-bold;
    }
    
    &.dots {
      border: none;
      
      &:hover {
        background: transparent;
        color: $text-color;
      }
    }
  }
  
  @media (max-width: 480px) {
    flex-wrap: wrap;
    
    .page-numbers {
      margin: 0.25rem;
    }
  }
}
