/**
 * Tipografia do Tema UENF
 * Estilos para textos e elementos de tipografia
 */

// Estilos de corpo
body {
  font-family: $font-secondary;
  font-size: $font-size-base;
  font-weight: $font-weight-normal;
  line-height: 1.5;
  color: $text-color;
  text-align: left;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// Links
// ------

a {
  color: $link-color;
  text-decoration: none;
  background-color: transparent;
  transition: color 0.2s ease-in-out, background-color 0.2s ease-in-out, border-color 0.2s ease-in-out;
  
  &:hover {
    color: $link-hover-color;
    text-decoration: underline;
  }
  
  // Links sem href ainda devem parecer clicáveis
  &:not([href]):not([class]) {
    &,
    &:hover {
      color: inherit;
      text-decoration: none;
    }
  }
}

// Títulos
// -------

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  margin-top: 0;
  margin-bottom: $spacer;
  font-family: $font-primary;
  font-weight: $font-weight-bold;
  line-height: 1.2;
  color: $primary-color;
}

h1, .h1 { 
  font-size: $font-size-xxl;
  margin-bottom: $spacer * 1.5;
}

h2, .h2 { 
  font-size: $font-size-xl;
  margin-bottom: $spacer * 1.25;
  padding-bottom: $spacer * 0.5;
  border-bottom: 1px solid rgba($primary-color, 0.1);
}

h3, .h3 { 
  font-size: $font-size-lg;
  margin-bottom: $spacer;
}

h4, .h4 { 
  font-size: $font-size-base * 1.1;
  font-weight: $font-weight-medium;
  margin-bottom: $spacer * 0.75;
}

h5, .h5 { 
  font-size: $font-size-sm;
  font-weight: $font-weight-medium;
  margin-bottom: $spacer * 0.5;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

h6, .h6 { 
  font-size: $font-size-sm;
  font-weight: $font-weight-normal;
  margin-bottom: $spacer * 0.5;
  color: lighten($text-color, 15%);
}

// Parágrafos
// ----------

p {
  margin-top: 0;
  margin-bottom: $spacer;
  
  &:last-child {
    margin-bottom: 0;
  }
}

.lead {
  font-size: $font-size-lg;
  font-weight: $font-weight-light;
  line-height: 1.6;
}

// Listas
// ------

ul, ol, dl {
  margin-top: 0;
  margin-bottom: $spacer;
  padding-left: 1.5rem;
  
  ul, ol {
    margin-bottom: 0;
  }
}

ul {
  list-style-type: disc;
  
  ul {
    list-style-type: circle;
    
    ul {
      list-style-type: square;
    }
  }
}

ol {
  list-style-type: decimal;
  
  ol {
    list-style-type: lower-roman;
    
    ol {
      list-style-type: lower-alpha;
    }
  }
}

dt {
  font-weight: $font-weight-bold;
  margin-top: $spacer * 0.5;
  
  &:first-child {
    margin-top: 0;
  }
}

dd {
  margin-left: 0;
  margin-bottom: $spacer * 0.5;
}

// Texto destacado
// ---------------

mark, .mark {
  padding: 0.2em;
  background-color: #fff3cd;
}

small, .small {
  font-size: 80%;
  font-weight: $font-weight-normal;
}

// Citações
// --------

blockquote {
  margin: 0 0 $spacer;
  padding: $spacer $spacer * 1.5;
  border-left: 4px solid $primary-color;
  font-style: italic;
  background-color: rgba($primary-color, 0.05);
  
  p:last-child {
    margin-bottom: 0;
  }
  
  footer {
    display: block;
    font-size: 80%;
    color: lighten($text-color, 20%);
    
    &::before {
      content: '— ';
    }
  }
}

// Código
// ------

code, kbd, pre, samp {
  font-family: SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
  font-size: 0.9em;
}

code {
  padding: 0.2rem 0.4rem;
  font-size: 90%;
  color: #e83e8c;
  background-color: #f8f9fa;
  border-radius: $border-radius;
}

pre {
  display: block;
  margin-top: 0;
  margin-bottom: $spacer;
  overflow: auto;
  font-size: 0.9em;
  line-height: 1.5;
  color: #212529;
  background-color: #f8f9fa;
  border: 1px solid #dee2e6;
  border-radius: $border-radius;
  padding: $spacer;
  
  code {
    padding: 0;
    font-size: inherit;
    color: inherit;
    background-color: transparent;
    border-radius: 0;
  }
}

kbd {
  padding: 0.2rem 0.4rem;
  font-size: 90%;
  color: #fff;
  background-color: #212529;
  border-radius: 0.2rem;
  
  kbd {
    padding: 0;
    font-size: 100%;
    font-weight: $font-weight-bold;
  }
}

// Texto alinhado
// --------------

.text-left { text-align: left !important; }
.text-right { text-align: right !important; }
.text-center { text-align: center !important; }
.text-justify { text-align: justify !important; }
.text-nowrap { white-space: nowrap !important; }

// Transformação de texto
// ---------------------

.text-lowercase { text-transform: lowercase !important; }
.text-uppercase { text-transform: uppercase !important; }
.text-capitalize { text-transform: capitalize !important; }

// Pesos de fonte
// --------------

.font-weight-light { font-weight: $font-weight-light !important; }
.font-weight-normal { font-weight: $font-weight-normal !important; }
.font-weight-medium { font-weight: $font-weight-medium !important; }
.font-weight-bold { font-weight: $font-weight-bold !important; }
.font-italic { font-style: italic !important; }

// Cores de texto
// --------------

.text-primary { color: $primary-color !important; }
.text-secondary { color: $text-color !important; }
.text-success { color: $success !important; }
.text-info { color: $info !important; }
.text-warning { color: $warning !important; }
.text-danger { color: $danger !important; }
.text-light { color: #f8f9fa !important; }
.text-dark { color: #212529 !important; }
.text-muted { color: #6c757d !important; }
.text-white { color: #fff !important; }

// Cores de fundo
// --------------

.bg-primary { background-color: $primary-color !important; }
.bg-secondary { background-color: #6c757d !important; }
.bg-success { background-color: $success !important; }
.bg-info { background-color: $info !important; }
.bg-warning { background-color: $warning !important; }
.bg-danger { background-color: $danger !important; }
.bg-light { background-color: #f8f9fa !important; }
.bg-dark { background-color: #212529 !important; }
.bg-white { background-color: #fff !important; }

// Tamanhos de texto responsivos
// ----------------------------

@each $breakpoint in map-keys($breakpoints) {
  @include respond-to($breakpoint) {
    .text-#{$breakpoint}-left { text-align: left !important; }
    .text-#{$breakpoint}-right { text-align: right !important; }
    .text-#{$breakpoint}-center { text-align: center !important; }
  }
}

// Utilitários de texto
// -------------------

.text-truncate {
  @include text-truncate();
}

.text-hide {
  @include text-hide();
}

// Visibilidade
// -----------

.invisible {
  visibility: hidden !important;
}

// Esconde conteúdo visualmente, mas mantém acessível para leitores de tela
.sr-only {
  @include sr-only();
}

// Força o elemento a ocupar 100% da largura
.w-100 {
  width: 100% !important;
}

// Força o elemento a ocupar 100% da altura
.h-100 {
  height: 100% !important;
}
