/**
 * Mixins do Tema UENF
 * 
 * Este arquivo contém mixins (funções reutilizáveis) para estilização consistente.
 * Os mixins estão organizados em seções lógicas para facilitar a manutenção.
 * 
 * Uso recomendado:
 * @use 'config/mixins' as *;  // Importar em outros arquivos SCSS
 * .elemento { @include mixin-name(parametros); }
 */

// ==========================================================================
// 1. Media Queries
// ==========================================================================

// Breakpoints do Bootstrap para referência
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
) !default;

/**
 * 1.1 respond-to
 * Gera media queries baseadas em breakpoints definidos em $breakpoints
 * 
 * @param {String} $breakpoint - Nome do breakpoint (ex: 'sm', 'md', 'lg')
 * @content Estilos a serem aplicados dentro da media query
 * 
 * Exemplo de uso:
 * @include respond-to('md') { ... }
 */
@mixin respond-to($breakpoint) {
  @if map-has-key($breakpoints, $breakpoint) {
    $value: map-get($breakpoints, $breakpoint);
    @media (min-width: $value) {
      @content;
    }
  } @else {
    @warn "O breakpoint `#{$breakpoint}` não foi encontrado em $breakpoints.";
  }
}

/**
 * 1.2 media-breakpoint-up
 * Mixin para criar media queries que se aplicam a partir de um determinado breakpoint
 * Similar ao Bootstrap
 * 
 * @param {String} $name - Nome do breakpoint (ex: 'sm', 'md', 'lg')
 * @content Estilos a serem aplicados dentro da media query
 * 
 * Exemplo de uso:
 * @include media-breakpoint-up('md') { ... }
 */
@mixin media-breakpoint-up($name) {
  $min: map-get($grid-breakpoints, $name);
  @if $min {
    @media (min-width: $min) {
      @content;
    }
  } @else {
    @content;
  }
}

/**
 * 1.3 media-breakpoint-down
 * Mixin para criar media queries que se aplicam até um determinado breakpoint
 * Similar ao Bootstrap
 * 
 * @param {String} $name - Nome do breakpoint (ex: 'sm', 'md', 'lg')
 * @content Estilos a serem aplicados dentro da media query
 * 
 * Exemplo de uso:
 * @include media-breakpoint-down('md') { ... }
 */
@mixin media-breakpoint-down($name) {
  $max: map-get($grid-breakpoints, $name);
  @if $max {
    @media (max-width: ($max - 0.02)) {
      @content;
    }
  } @else {
    @content;
  }
}

/**
 * 1.4 breakpoint-infix
 * Função auxiliar para gerar o sufixo de classe para breakpoints
 * 
 * @param {String} $name - Nome do breakpoint
 * @param {Map} $breakpoints - Mapa de breakpoints
 * @return {String} - Sufixo da classe (ex: '-md', '-lg' ou '')
 */
@function breakpoint-infix($name, $breakpoints) {
  @return if(breakpoint-min($name, $breakpoints) == null, '', '-#{$name}');
}

/**
 * 1.5 breakpoint-min
 * Função auxiliar para obter o valor mínimo de um breakpoint
 * 
 * @param {String} $name - Nome do breakpoint
 * @param {Map} $breakpoints - Mapa de breakpoints
 * @return {Number} - Valor mínimo do breakpoint ou null
 */
@function breakpoint-min($name, $breakpoints) {
  $min: map-get($breakpoints, $name);
  @return if($min != 0, $min, null);
}

// ==========================================================================
// 2. Tipografia
// ==========================================================================

/**
 * 2.1 reset-list
 * Remove estilos padrão de listas (margens, paddings e marcadores)
 * 
 * Exemplo de uso:
 * ul, ol {
 *   @include reset-list;
 * }
 */
@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

/**
 * 2.2 text-hide
 * Esconde o texto visualmente, mantendo-o acessível para leitores de tela
 * Útil para elementos que usam ícones ou imagens mas precisam de texto alternativo
 * 
 * Exemplo de uso:
 * .logo { 
 *   @include text-hide();
 *   background: url(logo.png) no-repeat;
 * }
 */
@mixin text-hide() {
  font: 0/0 a;
  color: transparent;
  text-shadow: none;
  background-color: transparent;
  border: 0;
}

/**
 * 2.2 font
 * Define propriedades de fonte de forma simplificada
 * 
 * @param {Number} $size - Tamanho da fonte (opcional)
 * @param {Number} $weight - Peso da fonte (opcional)
 * @param {String} $family - Família da fonte (opcional)
 * 
 * Exemplo de uso:
 * .titulo { @include font(1.5rem, $font-weight-bold, $font-primary); }
 */
@mixin font($size: null, $weight: null, $family: null) {
  @if $size { font-size: $size; }
  @if $weight { font-weight: $weight; }
  @if $family { font-family: $family; }
}

/**
 * 2.3 link-style
 * Estiliza links com estados hover/focus consistentes
 * 
 * @param {Color} $color - Cor padrão do link (padrão: $link-color)
 * @param {Color} $hover - Cor no hover/focus (padrão: $link-hover-color)
 * 
 * Exemplo de uso:
 * a { @include link-style($primary-color, $primary-dark); }
 */
@mixin link-style($color: $link-color, $hover: $link-hover-color) {
  color: $color;
  text-decoration: none;
  transition: color 0.2s ease-in-out;
  
  &:hover,
  &:focus {
    color: $hover;
    text-decoration: underline;
  }
}

// ==========================================================================
// 3. Layout
// ==========================================================================

/**
 * 3.1 center
 * Centraliza um elemento de forma absoluta em relação ao seu contêiner
 * 
 * @param {String} $position - Tipo de centralização: 'both' (padrão), 'vertical' ou 'horizontal'
 * 
 * Exemplo de uso:
 * .elemento {
 *   @include center(); // Centraliza nos dois eixos
 *   @include center('vertical'); // Centraliza apenas verticalmente
 *   @include center('horizontal'); // Centraliza apenas horizontalmente
 * }
 */
@mixin center($position: 'both') {
  position: absolute;
  
  @if $position == 'vertical' {
    top: 50%;
    transform: translateY(-50%);
  } @else if $position == 'horizontal' {
    left: 50%;
    transform: translateX(-50%);
  } @else {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

/**
 * 3.2 container
 * Cria um contêiner centralizado com largura máxima e padding lateral responsivo
 * 
 * @param {Length} $max-width - Largura máxima do contêiner (padrão: 1200px)
 * 
 * Exemplo de uso:
 * .container {
 *   @include container(1140px);
 * }
 */
@mixin container($max-width: 1200px) {
  width: 100%;
  max-width: $max-width;
  margin-left: auto;
  margin-right: auto;
  padding-left: map-get($spacers, 3);
  padding-right: map-get($spacers, 3);
  
  @include respond-to('md') {
    padding-left: map-get($spacers, 4);
    padding-right: map-get($spacers, 4);
  }
}

// ==========================================================================
 // Componentes
// ==========================================================================

/// Estilo de botão
/// @param {Color} $background - Cor de fundo
/// @param {Color} $color - Cor do texto
@mixin button-variant($background, $color: $white) {
  background-color: $background;
  color: $color;
  border: 1px solid darken($background, 5%);
  border-radius: $border-radius;
  padding: 0.5rem 1rem;
  font-weight: $font-weight-medium;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
  
  &:hover, &:focus {
    background-color: darken($background, 7.5%);
    border-color: darken($background, 10%);
    color: $color;
    text-decoration: none;
  }
  
  &:active {
    background-color: darken($background, 10%);
    border-color: darken($background, 12.5%);
  }
}

/// Estilo de card
/// @param {Number} $padding - Espaçamento interno
@mixin card($padding: 1.5rem) {
  background: $white;
  border-radius: $border-radius;
  box-shadow: 0 0.125rem 0.25rem rgba($black, 0.075);
  padding: $padding;
  margin-bottom: map-get($spacers, 4);
  
  @include respond-to('md') {
    box-shadow: 0 0.5rem 1rem rgba($black, 0.15);
  }
}

// ==========================================================================
 // Utilidades
// ==========================================================================

/// Esconde texto mostrando apenas para leitores de tela
@mixin sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/// Limpa os floats
@mixin clearfix {
  &::after {
    content: "";
    display: table;
    clear: both;
  }
}

/// Truncate text with ellipsis
@mixin text-truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

/// Adiciona efeito de hover suave
@mixin hover-focus {
  &:hover, &:focus {
    @content;
  }
}
