/**
 * Utilitários: Helpers
 * Classes auxiliares para estilos comuns
 */

// Texto
.text-center { text-align: center !important; }
.text-left { text-align: left !important; }
.text-right { text-align: right !important; }
.text-uppercase { text-transform: uppercase !important; }
.text-nowrap { white-space: nowrap !important; }
.text-truncate {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

// Cores de texto
.text-primary { color: $primary-color !important; }
.text-secondary { color: $secondary-color !important; }
.text-muted { color: #6c757d !important; }
.text-white { color: $white !important; }

// Cores de fundo
.bg-primary { background-color: $primary-color !important; }
.bg-light { background-color: $light-color !important; }
.bg-white { background-color: $white !important; }

// Margens e preenchimentos
@for $i from 0 through 5 {
  .m-#{$i} { margin: #{$i}rem !important; }
  .mt-#{$i} { margin-top: #{$i}rem !important; }
  .mb-#{$i} { margin-bottom: #{$i}rem !important; }
  .ml-#{$i} { margin-left: #{$i}rem !important; }
  .mr-#{$i} { margin-right: #{$i}rem !important; }
  
  .p-#{$i} { padding: #{$i}rem !important; }
  .pt-#{$i} { padding-top: #{$i}rem !important; }
  .pb-#{$i} { padding-bottom: #{$i}rem !important; }
  .pl-#{$i} { padding-left: #{$i}rem !important; }
  .pr-#{$i} { padding-right: #{$i}rem !important; }
}

// Display
.d-none { display: none !important; }
.d-block { display: block !important; }
.d-flex { display: flex !important; }
.d-inline-block { display: inline-block !important; }

// Flexbox
.flex-column { flex-direction: column !important; }
.flex-wrap { flex-wrap: wrap !important; }
.justify-content-between { justify-content: space-between !important; }
.justify-content-center { justify-content: center !important; }
.align-items-center { align-items: center !important; }

// Responsividade
@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);
    
    .d#{$infix}-none { display: none !important; }
    .d#{$infix}-block { display: block !important; }
    .d#{$infix}-flex { display: flex !important; }
    
    .flex#{$infix}-column { flex-direction: column !important; }
    .flex#{$infix}-row { flex-direction: row !important; }
    
    .justify-content#{$infix}-between { justify-content: space-between !important; }
    .justify-content#{$infix}-center { justify-content: center !important; }
    .align-items#{$infix}-center { align-items: center !important; }
  }
}
