/**
 * Componente: Widgets
 * Estilos para widgets do WordPress
 */

.widget {
  margin-bottom: 2rem;
  
  .widget-title {
    font-size: 1.25rem;
    margin: 0 0 1.25rem;
    padding-bottom: 0.75rem;
    border-bottom: 2px solid $primary-color;
    color: $primary-color;
    font-weight: $font-weight-bold;
  }
  
  // Widget de busca
  &.widget_search {
    .search-form {
      position: relative;
      
      .search-field {
        width: 100%;
        padding: 0.75rem 1rem;
        border: 1px solid #dee2e6;
        border-radius: $btn-border-radius;
        
        &:focus {
          border-color: $primary-color;
          outline: none;
          box-shadow: 0 0 0 0.2rem rgba($primary-color, 0.25);
        }
      }
      
      .search-submit {
        position: absolute;
        right: 0.5rem;
        top: 50%;
        transform: translateY(-50%);
        background: none;
        border: none;
        color: $text-color;
        cursor: pointer;
        
        &:hover { color: $primary-color; }
      }
    }
  }
  
  // Widget de lista
  &.widget_categories,
  &.widget_archive,
  &.widget_pages,
  &.widget_meta {
    ul {
      list-style: none;
      margin: 0;
      padding: 0;
      
      li {
        margin-bottom: 0.5rem;
        padding-left: 1.25rem;
        position: relative;
        
        &::before {
          content: '\f054';
          font-family: 'Font Awesome 5 Free';
          font-weight: 900;
          font-size: 0.625rem;
          position: absolute;
          left: 0;
          top: 0.5rem;
          color: $primary-color;
        }
        
        a {
          color: $text-color;
          text-decoration: none;
          
          &:hover { color: $primary-color; }
        }
      }
    }
  }
  
  // Widget de tags
  &.widget_tag_cloud {
    .tagcloud {
      display: flex;
      flex-wrap: wrap;
      gap: 0.5rem;
      
      a {
        display: inline-block;
        background: #f8f9fa;
        color: $text-color;
        padding: 0.25rem 0.75rem;
        border-radius: 50px;
        font-size: 0.8125rem !important;
        text-decoration: none;
        transition: all 0.2s ease;
        
        &:hover {
          background: $primary-color;
          color: $white;
        }
      }
    }
  }
  
  // Widget de posts recentes
  &.widget_recent_entries,
  &.widget_recent_comments {
    ul {
      list-style: none;
      margin: 0;
      padding: 0;
      
      li {
        margin-bottom: 1rem;
        padding-bottom: 1rem;
        border-bottom: 1px solid #eee;
        
        &:last-child {
          margin-bottom: 0;
          padding-bottom: 0;
          border-bottom: none;
        }
        
        a {
          color: $text-color;
          text-decoration: none;
          font-weight: $font-weight-medium;
          display: block;
          margin-bottom: 0.25rem;
          
          &:hover { color: $primary-color; }
        }
        
        .post-date {
          display: block;
          font-size: 0.8125rem;
          color: #6c757d;
        }
      }
    }
  }
  
  // Widget de texto
  &.widget_text {
    p:last-child { margin-bottom: 0; }
  }
}
