/**
 * Páginas: Arquivos
 * Estilos para páginas de arquivo (categorias, tags, busca, etc.)
 */

.archive-header,
.search-header {
  margin-bottom: 2.5rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #eee;
  text-align: center;
  
  .archive-title,
  .search-title {
    font-size: 2rem;
    color: $primary-color;
    margin: 0 0 0.5rem;
  }
  
  .archive-description,
  .search-description {
    color: #6c757d;
    font-size: 1.1rem;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .search-query {
    font-weight: $font-weight-bold;
    color: $primary-color;
  }
}

// Grid de posts
.posts-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 2rem;
  margin: 2rem 0;
  
  @media (max-width: 767px) {
    grid-template-columns: 1fr;
  }
  
  .post-card {
    background: $white;
    border-radius: $btn-border-radius;
    overflow: hidden;
    box-shadow: 0 2px 15px rgba(0, 0, 0, 0.05);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    
    &:hover {
      transform: translateY(-5px);
      box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
    }
    
    .post-thumbnail {
      height: 200px;
      overflow: hidden;
      
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: transform 0.5s ease;
      }
      
      &:hover img {
        transform: scale(1.05);
      }
    }
    
    .post-content {
      padding: 1.5rem;
      
      .entry-header {
        margin-bottom: 1rem;
        
        .entry-title {
          font-size: 1.25rem;
          margin: 0 0 0.75rem;
          line-height: 1.4;
          
          a {
            color: $text-color;
            text-decoration: none;
            
            &:hover {
              color: $primary-color;
            }
          }
        }
        
        .entry-meta {
          font-size: 0.8125rem;
          color: #6c757d;
          
          span {
            margin-right: 1rem;
            
            &:last-child {
              margin-right: 0;
            }
            
            a {
              color: inherit;
              text-decoration: none;
              
              &:hover {
                color: $primary-color;
                text-decoration: underline;
              }
            }
            
            i {
              color: $primary-color;
              margin-right: 0.25rem;
            }
          }
        }
      }
      
      .entry-summary {
        color: #495057;
        margin-bottom: 1.5rem;
        line-height: 1.7;
        
        p {
          margin: 0 0 1rem;
          
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
      
      .read-more {
        display: inline-flex;
        align-items: center;
        color: $primary-color;
        font-weight: $font-weight-medium;
        text-decoration: none;
        
        &:hover {
          text-decoration: underline;
        }
        
        &::after {
          content: '→';
          margin-left: 0.5rem;
          transition: transform 0.2s ease;
        }
        
        &:hover::after {
          transform: translateX(3px);
        }
      }
    }
  }
  
  // Estilo para posts sem imagem destacada
  .no-thumbnail {
    .post-content {
      padding-top: 2rem;
    }
  }
}

// Navegação de posts
.pagination {
  margin: 3rem 0;
  text-align: center;
  
  .nav-links {
    display: inline-flex;
    gap: 0.5rem;
    
    .page-numbers {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background: $white;
      color: $text-color;
      text-decoration: none;
      font-weight: $font-weight-medium;
      transition: all 0.2s ease;
      
      &:hover:not(.current) {
        background: $primary-color;
        color: $white;
      }
      
      &.current {
        background: $primary-color;
        color: $white;
        font-weight: $font-weight-bold;
      }
      
      &.dots {
        background: transparent;
        color: $text-color;
        pointer-events: none;
      }
      
      &.prev,
      &.next {
        width: auto;
        padding: 0 1rem;
        border-radius: 50px;
        
        i {
          font-style: normal;
          margin: 0 0.25rem;
        }
      }
    }
  }
}

// Mensagem de nenhum resultado
.no-results {
  text-align: center;
  padding: 3rem 1rem;
  background: #f8f9fa;
  border-radius: $btn-border-radius;
  
  .page-title {
    color: $primary-color;
    margin-bottom: 1rem;
  }
  
  p {
    margin-bottom: 1.5rem;
    color: #6c757d;
  }
  
  .search-form {
    max-width: 500px;
    margin: 0 auto;
    
    .search-field {
      width: 100%;
      padding: 0.75rem 1rem;
      border: 1px solid #dee2e6;
      border-radius: $btn-border-radius 0 0 $btn-border-radius;
      font-size: 1rem;
      
      &:focus {
        outline: none;
        border-color: $primary-color;
      }
    }
    
    .search-submit {
      background: $primary-color;
      color: $white;
      border: none;
      padding: 0.75rem 1.5rem;
      border-radius: 0 $btn-border-radius $btn-border-radius 0;
      cursor: pointer;
      font-weight: $font-weight-medium;
      transition: background-color 0.2s ease;
      
      &:hover {
        background: darken($primary-color, 10%);
      }
    }
  }
}
