// Reset CSS para o Tema UENF
// Baseado em Normalize.css e customizações específicas

// Box sizing reset
*, *::before, *::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

// Configurações base do HTML e Body
html {
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
  height: 100%;
}

body {
  margin: 0;
  font-family: $font-secondary;
  font-size: $font-size-base;
  font-weight: $font-weight-normal;
  line-height: 1.5;
  color: $text-color;
  background-color: $body-bg;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

// Links
// ------
a {
  color: $link-color;
  text-decoration: none;
  background-color: transparent;
  transition: color 0.2s ease-in-out;
  
  &:hover {
    color: $link-hover-color;
    text-decoration: underline;
  }
}

// Títulos
h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: $spacer;
  font-family: $font-primary;
  font-weight: $font-weight-bold;
  line-height: 1.2;
  color: $primary-color;
}

h1 { font-size: $font-size-xxl; }
h2 { font-size: $font-size-xl; }
h3 { font-size: $font-size-lg; }
h4 { font-size: $font-size-base; }
h5 { font-size: $font-size-sm; }
h6 { font-size: $font-size-sm; font-weight: $font-weight-normal; }

// Parágrafos e listas
p, ol, ul, dl {
  margin-top: 0;
  margin-bottom: $spacer;
}

// Imagens e elementos incorporados
img, svg, video, canvas, audio, iframe, embed, object {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
  border-style: none;
}

// Tabelas
table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: $spacer;
  border: 1px solid #dee2e6;
  
  th, td {
    padding: 0.75rem;
    vertical-align: top;
    border: 1px solid #dee2e6;
  }
  
  thead th {
    vertical-align: bottom;
    border-bottom: 2px solid #dee2e6;
    background-color: #f8f9fa;
  }
}

// Formulários
button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: inherit;
  line-height: inherit;
}

button, input {
  overflow: visible;
}

button, select {
  text-transform: none;
}

[role="button"] {
  cursor: pointer;
}

// Remove the inheritance of text transform in Firefox
button, select {
  text-transform: none;
}

// Remove the margin in Firefox and Safari
button, input, optgroup, select, textarea {
  margin: 0;
}

// Remove the inheritance of word-wrap in Safari
button, select {
  text-transform: none;
}

// Correct the inability to style clickable types in iOS and Safari
button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

// Remove inner border and padding from Firefox, but don't restore the outline like Normalize
button::-moz-focus-inner, [type="button"]::-moz-focus-inner, [type="reset"]::-moz-focus-inner, [type="submit"]::-moz-focus-inner {
  padding: 0;
  border-style: none;
}

// 1. Add the correct box sizing in IE 10-
// 2. Remove the padding in IE 10-
[type="checkbox"], [type="radio"] {
  box-sizing: border-box;
  padding: 0;
}

// Correct the cursor style of increment and decrement buttons in Chrome
[type="number"]::-webkit-inner-spin-button, [type="number"]::-webkit-outer-spin-button {
  height: auto;
}

// 1. Correct the odd appearance in Chrome and Safari
// 2. Correct the outline style in Safari
[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

// Remove the inner padding in Chrome and Safari on macOS
[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}

// 1. Correct the inability to style clickable types in iOS and Safari
// 2. Change font properties to inherit in Safari
::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

// Remove border on images inside links in IE 10
a img {
  border-style: none;
}

// Hide the overflow in IE
svg:not(:root) {
  overflow: hidden;
}

// Add the correct display in IE 10+
template {
  display: none;
}

// Always hide an element with the hidden HTML attribute
[hidden] {
  display: none !important;
}
