/**
 * Componente: Botões
 * Estilos para botões e elementos interativos
 */

// Variáveis para botões
$btn-padding-y: 0.5rem;
$btn-padding-x: 1.5rem;
$btn-font-family: $font-primary;
$btn-font-weight: $font-weight-medium;
$btn-line-height: 1.5;
$btn-border-radius: $border-radius;
$btn-transition: all 0.2s ease-in-out;

// Estilo base para botões
.btn {
  display: inline-block;
  font-family: $btn-font-family;
  font-weight: $btn-font-weight;
  line-height: $btn-line-height;
  color: $white;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  user-select: none;
  background-color: $primary-color;
  border: 1px solid transparent;
  padding: $btn-padding-y $btn-padding-x;
  font-size: $font-size-base;
  border-radius: $btn-border-radius;
  transition: $btn-transition;
  
  // Remover contorno em alguns navegadores
  &:focus,
  &.focus {
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgba($primary-color, 0.25);
  }
  
  // Desabilitado e estado de carregamento
  &:disabled,
  &.disabled,
  fieldset:disabled & {
    opacity: 0.65;
    pointer-events: none;
  }
  
  // Remove o sublinhado em links estilizados como botões
  &:hover {
    color: $white;
    text-decoration: none;
  }
}

// Variantes de botão
// -----------------

// Botão primário
.btn-primary {
  @include button-variant($primary-color, $white);
  
  &:hover, 
  &:focus,
  &.focus,
  &:active,
  &.active {
    background-color: darken($primary-color, 7.5%);
    border-color: darken($primary-color, 10%);
  }
  
  &:disabled,
  &.disabled {
    background-color: $primary-color;
    border-color: $primary-color;
  }
}

// Botão secundário
.btn-secondary {
  @include button-variant(#6c757d, $white);
  
  &:hover, 
  &:focus,
  &.focus,
  &:active,
  &.active {
    background-color: #5a6268;
    border-color: #545b62;
  }
  
  &:disabled,
  &.disabled {
    background-color: #6c757d;
    border-color: #6c757d;
  }
}

// Botão de sucesso
.btn-success {
  @include button-variant($success, $white);
  
  &:hover, 
  &:focus,
  &.focus,
  &:active,
  &.active {
    background-color: #218838;
    border-color: #1e7e34;
  }
}

// Botão de perigo
.btn-danger {
  @include button-variant($danger, $white);
  
  &:hover, 
  &:focus,
  &.focus,
  &:active,
  &.active {
    background-color: #c82333;
    border-color: #bd2130;
  }
}

// Botão de aviso
.btn-warning {
  @include button-variant($warning, $black);
  
  &:hover, 
  &:focus,
  &.focus,
  &:active,
  &.active {
    background-color: #e0a800;
    border-color: #d39e00;
  }
}

// Botão de informação
.btn-info {
  @include button-variant($info, $white);
  
  &:hover, 
  &:focus,
  &.focus,
  &:active,
  &.active {
    background-color: #138496;
    border-color: #117a8b;
  }
}

// Botão claro
.btn-light {
  @include button-variant(#f8f9fa, $black);
  
  &:hover, 
  &:focus,
  &.focus,
  &:active,
  &.active {
    background-color: #e2e6ea;
    border-color: #dae0e5;
  }
}

// Botão escuro
.btn-dark {
  @include button-variant(#343a40, $white);
  
  &:hover, 
  &:focus,
  &.focus,
  &:active,
  &.active {
    background-color: #23272b;
    border-color: #1d2124;
  }
}

// Botão de link
.btn-link {
  font-weight: $font-weight-normal;
  color: $link-color;
  text-decoration: none;
  background-color: transparent;
  border: 0;
  padding: 0;
  
  &:hover {
    color: $link-hover-color;
    text-decoration: underline;
  }
  
  &:focus,
  &.focus {
    text-decoration: underline;
    box-shadow: none;
  }
  
  &:disabled,
  &.disabled {
    color: #6c757d;
    pointer-events: none;
  }
}

// Tamanhos de botão
// ----------------

// Botão grande
.btn-lg {
  padding: 0.75rem 2rem;
  font-size: $font-size-lg;
  line-height: 1.5;
  border-radius: $btn-border-radius-lg;
}

// Botão pequeno
.btn-sm {
  padding: 0.25rem 0.8rem;
  font-size: $font-size-sm;
  line-height: 1.5;
  border-radius: $btn-border-radius-sm;
}

// Botão bloco
.btn-block {
  display: block;
  width: 100%;
  
  // Sobrescreve estilos inline
  + .btn-block {
    margin-top: 0.5rem;
  }
}

// Botão com ícone
.btn-icon {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  
  .icon {
    margin-right: 0.5rem;
    font-size: 1.2em;
    line-height: 1;
  }
  
  &.icon-right {
    flex-direction: row-reverse;
    
    .icon {
      margin-right: 0;
      margin-left: 0.5rem;
    }
  }
}

// Botão com ícone apenas
.btn-icon-only {
  width: 2.5rem;
  height: 2.5rem;
  padding: 0;
  line-height: 2.5rem;
  text-align: center;
  
  .icon {
    margin: 0;
    font-size: 1.25rem;
    line-height: 2.5rem;
  }
  
  &.btn-sm {
    width: 2rem;
    height: 2rem;
    line-height: 2rem;
    
    .icon {
      font-size: 1rem;
      line-height: 2rem;
    }
  }
  
  &.btn-lg {
    width: 3rem;
    height: 3rem;
    line-height: 3rem;
    
    .icon {
      font-size: 1.5rem;
      line-height: 3rem;
    }
  }
}

// Grupo de botões
.btn-group {
  position: relative;
  display: inline-flex;
  vertical-align: middle;
  
  > .btn {
    position: relative;
    flex: 1 1 auto;
    
    // Traz o botão selecionado para frente
    &:hover,
    &:focus,
    &:active,
    &.active {
      z-index: 1;
    }
  }
  
  // Remove bordas internas quando agrupados
  > .btn:not(:first-child),
  > .btn-group:not(:first-child) {
    margin-left: -1px;
  }
  
  // Remove borda esquerda quando não for o primeiro filho
  > .btn:not(:first-child) {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
  
  // Remove borda direita quando não for o último filho
  > .btn:not(:last-child):not(.dropdown-toggle) {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  
  // Remove borda direita do botão dropdown
  > .btn-group:not(:last-child) > .btn {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  
  // Remove borda esquerda do botão dropdown
  > .btn-group:not(:first-child) > .btn {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
}

// Grupo de botões vertical
.btn-group-vertical {
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  
  > .btn,
  > .btn-group {
    width: 100%;
  }
  
  // Remove bordas inferiores quando agrupados
  > .btn:not(:first-child),
  > .btn-group:not(:first-child) {
    margin-top: -1px;
    margin-left: 0;
  }
  
  // Remove borda superior quando não for o primeiro filho
  > .btn:not(:first-child) {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
  
  // Remove borda inferior quando não for o último filho
  > .btn:not(:last-child):not(.dropdown-toggle) {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
  
  // Remove borda inferior do botão dropdown
  > .btn-group:not(:last-child) > .btn {
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
  
  // Remove borda superior do botão dropdown
  > .btn-group:not(:first-child) > .btn {
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
}

// Botão de toggle
.btn-toggle {
  position: relative;
  padding: 0.5rem 1rem;
  color: rgba($black, 0.65);
  background-color: transparent;
  border: 0;
  
  &:hover,
  &:focus {
    color: rgba($black, 0.85);
    background-color: rgba($black, 0.05);
  }
  
  &.active {
    color: $primary-color;
    background-color: rgba($primary-color, 0.1);
  }
  
  // Estilo para botão de toggle com ícone
  .icon {
    margin-right: 0.5rem;
    font-size: 1.2em;
    line-height: 1;
    vertical-align: middle;
  }
}

// Botão flutuante (FAB)
.btn-fab {
  position: fixed;
  right: 2rem;
  bottom: 2rem;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  
  .icon {
    font-size: 1.5rem;
    line-height: 56px;
  }
  
  // Tamanhos
  &.btn-sm {
    width: 40px;
    height: 40px;
    
    .icon {
      font-size: 1.25rem;
      line-height: 40px;
    }
  }
  
  &.btn-lg {
    width: 64px;
    height: 64px;
    
    .icon {
      font-size: 2rem;
      line-height: 64px;
    }
  }
  
  // Posições
  &.top-left {
    top: 2rem;
    right: auto;
    left: 2rem;
    bottom: auto;
  }
  
  &.top-right {
    top: 2rem;
    right: 2rem;
    bottom: auto;
    left: auto;
  }
  
  &.bottom-left {
    top: auto;
    right: auto;
    bottom: 2rem;
    left: 2rem;
  }
  
  // Grupo de botões flutuantes
  + .btn-fab {
    margin-bottom: 1rem;
  }
}

// Grupo de botões flutuantes
.fab-container {
  position: fixed;
  right: 2rem;
  bottom: 2rem;
  z-index: 1000;
  
  .btn-fab {
    position: static;
    margin-bottom: 1rem;
    opacity: 0;
    transform: translateY(20px);
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    
    &.show {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  // Posições
  &.top-left {
    right: auto;
    left: 2rem;
    
    .btn-fab {
      margin-right: 0;
      margin-left: 0;
    }
  }
  
  &.top-right {
    right: 2rem;
    left: auto;
    bottom: auto;
    top: 2rem;
    
    .btn-fab {
      margin-bottom: 1rem;
    }
  }
  
  &.bottom-left {
    right: auto;
    left: 2rem;
    bottom: 2rem;
    
    .btn-fab {
      margin-bottom: 1rem;
    }
  }
  
  // Estilo para o botão principal do grupo
  .btn-fab-main {
    position: relative;
    z-index: 2;
    opacity: 1;
    transform: none;
    margin-bottom: 1rem;
  }
}
